'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import Link from 'next/link'

interface Challenge {
  id: string
  title: string
  description: string
  category: 'singing' | 'comedy' | 'dance' | 'storytelling' | 'creative'
  difficulty: 'easy' | 'medium' | 'hard'
  participants: number
  maxParticipants: number
  prize: string
  deadline: string
  isActive: boolean
  requirements: string[]
  exampleUrl?: string
}

const ChallengesPage = () => {
  const [challenges, setChallenges] = useState<Challenge[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedCategory, setSelectedCategory] = useState<string>('all')
  const [participatedChallenges, setParticipatedChallenges] = useState<Set<string>>(new Set())

  // Mock challenges data
  const mockChallenges: Challenge[] = [
    {
      id: '1',
      title: 'AI Twin Singing Challenge',
      description: 'Create your AI twin singing a popular song. Show off your twin\'s vocal abilities!',
      category: 'singing',
      difficulty: 'medium',
      participants: 234,
      maxParticipants: 500,
      prize: 'Featured on homepage + $100 prize',
      deadline: '2024-02-15',
      isActive: true,
      requirements: [
        'Use a popular song (copyright-free or with permission)',
        'Video must be 30-60 seconds long',
        'Include original audio comparison'
      ],
      exampleUrl: '/examples/singing-challenge'
    },
    {
      id: '2',
      title: 'Comedy Skit Showdown',
      description: 'Make people laugh with your AI twin in a comedy skit. Funniest twin wins!',
      category: 'comedy',
      difficulty: 'easy',
      participants: 156,
      maxParticipants: 300,
      prize: 'Comedy King/Queen badge + $50 prize',
      deadline: '2024-02-20',
      isActive: true,
      requirements: [
        'Original comedy content only',
        'Keep it family-friendly',
        'Maximum 2 minutes duration'
      ]
    },
    {
      id: '3',
      title: 'Dance Battle Royale',
      description: 'Show off your AI twin\'s dance moves in this epic dance battle!',
      category: 'dance',
      difficulty: 'hard',
      participants: 89,
      maxParticipants: 200,
      prize: 'Dance Master title + $200 prize',
      deadline: '2024-02-25',
      isActive: true,
      requirements: [
        'Choreograph original dance routine',
        'Sync with provided music track',
        'Include at least 3 different dance styles'
      ]
    },
    {
      id: '4',
      title: 'Storytelling Spectacular',
      description: 'Create a compelling story told by your AI twin. Best narrative wins!',
      category: 'storytelling',
      difficulty: 'medium',
      participants: 67,
      maxParticipants: 150,
      prize: 'Master Storyteller badge + Featured story',
      deadline: '2024-03-01',
      isActive: true,
      requirements: [
        'Original story (3-5 minutes)',
        'Clear narrative structure',
        'Engaging voice performance'
      ]
    },
    {
      id: '5',
      title: 'Creative Fusion Challenge',
      description: 'Combine multiple AI twin capabilities in one creative piece!',
      category: 'creative',
      difficulty: 'hard',
      participants: 45,
      maxParticipants: 100,
      prize: 'Innovation Award + $300 prize',
      deadline: '2024-03-10',
      isActive: true,
      requirements: [
        'Use at least 3 different AI features',
        'Show technical creativity',
        'Explain your creative process'
      ]
    },
    {
      id: '6',
      title: 'Voice Mimicry Masters',
      description: 'Challenge completed! Thanks to all participants.',
      category: 'singing',
      difficulty: 'medium',
      participants: 180,
      maxParticipants: 200,
      prize: 'Voice Master badge awarded',
      deadline: '2024-01-30',
      isActive: false,
      requirements: [
        'Mimic famous voices',
        'Include side-by-side comparison',
        'Rate accuracy'
      ]
    }
  ]

  const categories = [
    { value: 'all', label: 'All Challenges' },
    { value: 'singing', label: 'Singing' },
    { value: 'comedy', label: 'Comedy' },
    { value: 'dance', label: 'Dance' },
    { value: 'storytelling', label: 'Storytelling' },
    { value: 'creative', label: 'Creative' }
  ]

  useEffect(() => {
    // Simulate API call
    const fetchChallenges = async () => {
      try {
        setLoading(true)
        await new Promise(resolve => setTimeout(resolve, 1000))
        setChallenges(mockChallenges)
      } catch (error) {
        console.error('Failed to fetch challenges:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchChallenges()
  }, [])

  const handleParticipate = (challengeId: string) => {
    setParticipatedChallenges(prev => new Set([...prev, challengeId]))
    setChallenges(challenges.map(challenge => 
      challenge.id === challengeId 
        ? { ...challenge, participants: challenge.participants + 1 }
        : challenge
    ))
  }

  const filteredChallenges = challenges.filter(challenge => 
    selectedCategory === 'all' || challenge.category === selectedCategory
  )

  const activeChallenges = filteredChallenges.filter(c => c.isActive)
  const completedChallenges = filteredChallenges.filter(c => !c.isActive)

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'bg-green-500'
      case 'medium': return 'bg-yellow-500'
      case 'hard': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'singing': return 'bg-blue-500'
      case 'comedy': return 'bg-purple-500'
      case 'dance': return 'bg-pink-500'
      case 'storytelling': return 'bg-green-500'
      case 'creative': return 'bg-orange-500'
      default: return 'bg-gray-500'
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading challenges...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto space-y-6">
      <div className="text-center mb-8">
        <h1 className="text-4xl font-bold mb-4">AI Twin Challenges</h1>
        <p className="text-xl text-muted-foreground">
          Compete with creators worldwide and showcase your AI twin's abilities
        </p>
      </div>

      {/* Category Filter */}
      <div className="flex flex-wrap gap-2 justify-center mb-8">
        {categories.map((category) => (
          <Button
            key={category.value}
            variant={selectedCategory === category.value ? 'default' : 'outline'}
            onClick={() => setSelectedCategory(category.value)}
            size="sm"
          >
            {category.label}
          </Button>
        ))}
      </div>

      {/* Active Challenges */}
      <div className="space-y-6">
        <h2 className="text-2xl font-bold">Active Challenges</h2>
        
        {activeChallenges.length === 0 ? (
          <Card>
            <CardContent className="text-center py-8">
              <p className="text-muted-foreground">No active challenges in this category</p>
            </CardContent>
          </Card>
        ) : (
          <div className="grid md:grid-cols-2 gap-6">
            {activeChallenges.map((challenge) => (
              <Card key={challenge.id} className="overflow-hidden">
                <CardHeader>
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <CardTitle className="text-xl mb-2">{challenge.title}</CardTitle>
                      <div className="flex items-center space-x-2 mb-2">
                        <Badge className={`text-white ${getCategoryColor(challenge.category)}`}>
                          {challenge.category}
                        </Badge>
                        <Badge className={`text-white ${getDifficultyColor(challenge.difficulty)}`}>
                          {challenge.difficulty}
                        </Badge>
                      </div>
                    </div>
                  </div>
                </CardHeader>
                
                <CardContent className="space-y-4">
                  <p className="text-muted-foreground">{challenge.description}</p>
                  
                  {/* Progress */}
                  <div className="space-y-2">
                    <div className="flex justify-between text-sm">
                      <span>Participants</span>
                      <span>{challenge.participants}/{challenge.maxParticipants}</span>
                    </div>
                    <Progress 
                      value={(challenge.participants / challenge.maxParticipants) * 100} 
                      className="h-2"
                    />
                  </div>

                  {/* Prize */}
                  <div className="bg-muted p-3 rounded-lg">
                    <p className="text-sm font-medium text-primary">Prize</p>
                    <p className="text-sm">{challenge.prize}</p>
                  </div>

                  {/* Deadline */}
                  <div className="flex items-center justify-between text-sm">
                    <span className="text-muted-foreground">Deadline:</span>
                    <span className="font-medium">
                      {new Date(challenge.deadline).toLocaleDateString()}
                    </span>
                  </div>

                  {/* Requirements */}
                  <div className="space-y-2">
                    <p className="text-sm font-medium">Requirements:</p>
                    <ul className="text-sm text-muted-foreground space-y-1">
                      {challenge.requirements.map((req, index) => (
                        <li key={index} className="flex items-start">
                          <span className="mr-2">•</span>
                          <span>{req}</span>
                        </li>
                      ))}
                    </ul>
                  </div>

                  {/* Actions */}
                  <div className="flex space-x-2 pt-4">
                    {participatedChallenges.has(challenge.id) ? (
                      <Button disabled className="flex-1">
                        Participating
                      </Button>
                    ) : (
                      <Link href="/twin" className="flex-1">
                        <Button 
                          className="w-full"
                          onClick={() => handleParticipate(challenge.id)}
                        >
                          Join Challenge
                        </Button>
                      </Link>
                    )}
                    {challenge.exampleUrl && (
                      <Button variant="outline" size="sm">
                        View Example
                      </Button>
                    )}
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        )}
      </div>

      {/* Completed Challenges */}
      {completedChallenges.length > 0 && (
        <div className="space-y-6">
          <h2 className="text-2xl font-bold">Completed Challenges</h2>
          
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
            {completedChallenges.map((challenge) => (
              <Card key={challenge.id} className="opacity-75">
                <CardHeader>
                  <CardTitle className="text-lg">{challenge.title}</CardTitle>
                  <div className="flex items-center space-x-2">
                    <Badge variant="secondary">Completed</Badge>
                    <Badge className={`text-white ${getCategoryColor(challenge.category)}`}>
                      {challenge.category}
                    </Badge>
                  </div>
                </CardHeader>
                
                <CardContent>
                  <p className="text-sm text-muted-foreground mb-3">
                    {challenge.description}
                  </p>
                  
                  <div className="text-sm">
                    <p className="text-muted-foreground">
                      {challenge.participants} participants
                    </p>
                    <p className="text-primary font-medium">
                      {challenge.prize}
                    </p>
                  </div>
                  
                  <Button variant="outline" size="sm" className="w-full mt-3">
                    View Results
                  </Button>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      )}

      {/* Call to Action */}
      <Card className="bg-primary text-primary-foreground">
        <CardContent className="text-center py-8">
          <h3 className="text-2xl font-bold mb-4">Ready to Create Your AI Twin?</h3>
          <p className="mb-6 opacity-90">
            Join thousands of creators and start participating in challenges today!
          </p>
          <Link href="/twin">
            <Button variant="secondary" size="lg">
              Create Your First Twin
            </Button>
          </Link>
        </CardContent>
      </Card>
    </div>
  )
}

export default ChallengesPage
